html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #F783AC;
}

.nav__header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #F783AC;
}
.nav__header .nav__logo {
  padding: 1rem 0;
  width: 12rem;
  cursor: pointer;
}
@media (min-width: 768px) {
  .nav__header .nav__logo {
    padding: 2rem 0;
    width: 14rem;
  }
}
@media (min-width: 1024px) {
  .nav__header .nav__logo {
    width: 16rem;
  }
}
.nav__header .nav__a {
  display: none;
  font-size: 1.8rem;
}
@media (min-width: 768px) {
  .nav__header .nav__a {
    display: block;
  }
}
.nav__header .nav__a a {
  color: #FFFFFF;
  padding-right: 1rem;
}
.nav__header .nav__a a:hover {
  color: #cccccc;
}
.nav__header .nav__a a:last-of-type {
  padding-right: 0;
}
.nav__header .nav__menu {
  display: block;
  color: #FFFFFF;
  font-size: 2.8rem;
  cursor: pointer;
}
@media (min-width: 768px) {
  .nav__header .nav__menu {
    display: none;
  }
}

.header__hero {
  padding: 5rem 0;
  position: relative;
}
.header__hero::before {
  content: "";
  background-color: #000000;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  opacity: 0.25;
}

.hero {
  background-image: url("../img/banner.jpg");
  height: 30vh;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .hero {
    height: 35vh;
  }
}
@media (min-width: 1024px) {
  .hero {
    height: 42vh;
  }
}
.hero .text__hero {
  position: absolute;
  top: 30%;
  left: 30%;
  width: 30rem;
  font-size: 3rem;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  color: #FFFFFF;
  font-weight: bold;
}
@media (min-width: 768px) {
  .hero .text__hero {
    font-size: 5rem;
    top: 40%;
    left: 35%;
  }
}
@media (min-width: 1024px) {
  .hero .text__hero {
    font-size: 5rem;
    top: 40%;
    left: 40%;
  }
}
.hero .text__hero .label__hero {
  font-family: unset;
  font-weight: lighter;
  display: block;
  font-size: 1.8rem;
}
@media (min-width: 768px) {
  .hero .text__hero .label__hero {
    font-size: 2rem;
  }
}
@media (min-width: 1024px) {
  .hero .text__hero .label__hero {
    font-size: 2.5rem;
  }
}

.overlay {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: rgba(247, 131, 172, 0.9);
  overflow-x: hidden;
  transition: 0.5s;
}
.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 36px;
  color: #FFFFFF;
  display: block;
  transition: 0.3s;
}
.overlay a:hover, .overlay a:focus {
  color: #eceeef;
}
.overlay .closebtn {
  position: absolute;
  top: 20px;
  left: 48%;
  font-size: 60px;
}

.overlay-content {
  position: relative;
  top: 25%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

@media screen and (max-height: 450px) {
  .overlay a {
    font-size: 20px;
  }

  .overlay .closebtn {
    font-size: 40px;
    top: 15px;
    right: 35px;
  }
}
.products {
  padding: 2rem;
}
@media (min-width: 1024px) {
  .products {
    padding: 5rem;
  }
}
.products .products__grid {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  column-gap: 3rem;
  row-gap: 3rem;
}
@media (min-width: 768px) {
  .products .products__grid {
    flex-direction: row;
    flex-wrap: wrap;
    row-gap: 6rem;
  }
}
.products .grid__item {
  background-color: #FE99BC;
  border-radius: 1rem;
}
.products .grid__item img {
  border-radius: 1rem;
  width: 30rem;
}
.products .grid__item .item__link {
  border-radius: 1rem;
  display: block;
  text-align: center;
  padding: 1rem;
  color: #FFFFFF;
  font-weight: bold;
}

.products__title {
  padding: 2rem 0;
  font-size: 2.2rem;
  text-align: center;
  color: #F783AC;
}
@media (min-width: 768px) {
  .products__title {
    font-size: 2.8rem;
  }
}
@media (min-width: 1024px) {
  .products__title {
    font-size: 3.5rem;
  }
}

.contactus {
  padding: 2rem;
  background-color: #eceeef;
}
@media (min-width: 1024px) {
  .contactus {
    padding: 5rem;
  }
}
.contactus .contactus__flex {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  gap: 3rem;
  flex-direction: column;
  padding-bottom: 3rem;
}
@media (min-width: 768px) {
  .contactus .contactus__flex {
    flex-direction: row;
  }
}
@media (min-width: 1024px) {
  .contactus .contactus__flex {
    flex-direction: row;
  }
}
.contactus .flex__item img {
  width: 12rem;
  height: 12rem;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .contactus .flex__item img {
    width: 18rem;
    height: 18rem;
  }
}
@media (min-width: 1024px) {
  .contactus .flex__item img {
    width: 22rem;
    height: 22rem;
  }
}
.contactus .flex__text p {
  color: #F783AC;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .contactus .flex__text p {
    font-size: 2rem;
  }
}
.contactus .flex__text span {
  margin-right: 1rem;
}

.whatsapp {
  position: fixed;
  top: 90%;
  left: 2%;
  z-index: 2;
}
@media (min-width: 768px) {
  .whatsapp {
    top: 90%;
    left: 1%;
  }
}
.whatsapp i {
  color: #1bd844;
  font-size: 4rem;
}
.whatsapp i:hover {
  color: #3be25f;
}
@media (min-width: 768px) {
  .whatsapp i {
    font-size: 6rem;
  }
}

.site__footer {
  background-color: #F783AC;
}
.site__footer .credits__text {
  padding: 1rem 0 3rem 0;
  margin: 0;
  text-align: center;
  color: #FFFFFF;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .site__footer .credits__text {
    font-size: 2rem;
  }
}